.Phonebook {
  display: inline-flex;
  justify-content: center;
  display: inline-flex;
  padding: 0 50px 20px 50px;
  margin: 0;
  flex-direction: column;
  align-items: flex-start;
  height: 300px;
  box-shadow: 0 1px 3px 0 rgb(0 0 0 / 10%), 0 1px 1px 0 rgb(0 0 0 / 14%),
    0 2px 1px -1px rgb(0 0 0 / 12%);
  background-color: #e8f7fc;
}

input {
  border: 1px solid hsl(240, 3%, 40%);
  border-radius: 2px;
  border-color: hsl(223, 3%, 44%);
  box-shadow: 0 1px 2px 0 rgb(0 0 0 / 10%), 0 1px 1px 0 rgb(0 0 0 / 10%),
    0 2px 1px -1px rgb(0 0 0 / 10%);
}

Button {
  border-radius: 4px;
  padding: 2px 5px 2px 5px;
  border: 1px solid #cac9c8;
  cursor: pointer;
  font-family: Segoe UI;
  font-size: 12px;
  font-weight: 500;
  box-shadow: 0 1px 3px 0 rgb(0 0 0 / 10%), 0 1px 1px 0 rgb(0 0 0 / 10%),
    0 2px 1px -1px rgb(0 0 0 / 10%);
}

h2 {
  margin: 0p;
  padding: 0;
  margin-bottom: 35px;
  font-family: Segoe UI;
  font-size: 20px;
  font-weight: 500;
}
